*,*::after,*::before {
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

.page__header {
  background-image: linear-gradient(to left, #82ABA1, white);
}

.page__sections {
  margin-top: 25px;
  display: flex;
  flex-direction: row-reverse;
}

.page__main {
  border: 1px solid grey;
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.favourites__side {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.favourites__side ol {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.result__style {
  display: flex;
  flex-flow: wrap;
}

.result__style .swap__style {
  background-color: #242F2C;
  color: #82ABA1;
}

.result__style li {
  background-color: #82ABA1;
  color: #242F2C;
  margin: 10px;
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
}

.result__style img {
  display: inline;
}

.result__style p {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}
